html {
  font-family: "Source Sans Pro", sans-serif;
  font-style: normal;
  font-weight: 400; }

body {
  background: #eff3f6; }

.container {
  display: grid;
  grid-template-columns: 350px auto 350px;
  grid-template-areas: "sidebar header header""sidebar chart user-details""sidebar statistic user-details""sidebar referrer user-events"; }

.chart {
  grid-area: chart; }

.statistic {
  grid-area: statistic;
  background-color: yellow; }

.referrer {
  grid-area: referrer;
  background-color: green; }

.user-details {
  grid-area: user-details;
  background-color: blue; }

.user-events {
  grid-area: user-events;
  background-color: pink; }
